
<template>
    <div class="upcoming">
        <!-- 导航 -->
        <Movie-header :type="'movie'"></Movie-header>
        <!-- 电影主体 -->
        <main class="main-upcoming">
            <!-- 近期最受期待 -->
            <section class="Looking-forward">
                <span>近期最受期待</span>
                <!-- list -->
                <div class="Looking-list">
                    <ul class="Looking-list-ul">
                        <li v-for="i in Looking.coming" :key="i.id">
                            <!-- img -->
                            <div class="Looking-row-img">
                                <!-- love -->
                                <div class="love">
                                    <img
                                        src="@/assets/love.png"
                                        alt
                                        height="10"
                                    />
                                </div>
                                <img :src="i.img | WH('128.180')" />
                                <!-- look -->
                                <div class="look">{{ i.wish }}想看</div>
                            </div>
                            <!-- title -->
                            <div class="Looking-row-title">
                                <p>{{ i.nm }}</p>
                                <span>{{ i.comingTitle }}</span>
                            </div>
                        </li>
                    </ul>
                </div>
                <!-- list - end -->
            </section>
            <!-- 预售 - 列表 -->
            <section class="presell-list">
                <span>预售</span>
                <ul class="presell-list-ul">
                    <!-- row -->
                    <li class="Movie-row-img" v-for="i in presell" :key="i.id">
                        <!-- 电影图片 -->
                        <div class="Movie-row-img">
                            <img :src="i.img | WH('128.180')" />
                        </div>
                        <!-- 电影内容 -->
                        <div class="Movie-row-content">
                            <h3 class="starring">{{ i.nm }}</h3>
                            <p>
                                <span class="review">{{ i.wish }} </span>人想看
                                <br />
                                <span class="starring">主演 {{ i.star }}</span>
                                <!-- 播放 -->
                                {{ i.showInfo }}
                            </p>
                        </div>
                        <!-- 交互按钮 -->
                        <div class="Movie-row-interactive presell-interactive">
                            <button>预售</button>
                        </div>
                    </li>
                    <!-- row - end -->
                </ul>
            </section>
        </main>
        <!-- 脚注 -->
        <Movie-footer></Movie-footer>
    </div>
</template>



<script>
import Movie_header from "@/components/Movie-header";
import Movie_footer from "@/components/Movie-footer";
import axios from "axios";
import qs from "qs";

export default {
    name: "upcoming",
    components: {
        "Movie-footer": Movie_footer,
        "Movie-header": Movie_header
    },
    data() {
        return {
            // 近期最受期待
            Looking: [],
            presell: [],
            api: this.$store.state.api,
        };
    },
    methods: {
        // 获取近期最受期待电影
        OPEN_MovieData() {
            axios
                .get("http://m.maoyan.com/ajax/mostExpected?ci=418&limit=10&offset=0&token=")
                .then(res => {
                    this.Looking = res.data;
                    // console.log(this.Looking);
                })
                .catch(err => {
                    console.log(err);
                });
        },

        // 获取预售电影列表
        OPEN_PresellList() {
            axios
                .get(`${this.api}/api/movieComingList?cityId=418`)
                .then(res => {
                    this.presell = res.data.data.comingList;
                    // console.log(this.presell);
                })
                .catch(err => {
                    console.log(err);
                });
        }
    },
    mounted() {
        // 获取近期最受期待电影
        this.OPEN_MovieData();
        // 获取预售电影列表
        this.OPEN_PresellList();
    }
};
</script>


<style>
/* @import '../../../public/css/index.css'; */
.upcoming {
}

/* 电影主体 */
.main-upcoming {
    background-color: #f5f5f5;
    text-align: left;
    height: 100vh;
}

/* 近期 */
.Looking-forward {
    width: 100%;
    height: auto;
    background-color: #fff;
    padding: 4%;
    box-sizing: border-box;
}
.Looking-list {
    width: auto;
    overflow: auto;
    padding: 15px 0;
}
.Looking-list-ul {
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-inline-box;
    display: -moz-inline-box;
    width: auto;
    overflow: auto;
}
.Looking-list-ul > li {
    width: 100px;
    height: auto;
    /* background-color: aquamarine; */
    margin: 0 2rem 0 0;
}
.Looking-row-img {
    width: 100%;
    height: 127px;
    position: relative;
}
.Looking-row-img > img {
    width: 100%;
    height: 100%;
}
.love {
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.5);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom-right-radius: 10px;
}
.look {
    position: absolute;
    width: 100%;
    left: 4px;
    bottom: 2px;
    color: #faaf00;
    font-size: 0.9rem;
    font-weight: 600;
    text-align: center;
}

.Looking-row-title {
    padding: 0.4rem 0;
    box-sizing: border-box;
}
.Looking-row-title > p {
    font-size: 0.9rem;
    font-weight: bold;
    width: 5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.Looking-row-title > span {
    font-size: 0.7rem;
    color: #666;
}

/* 预售 - 列表  */
.presell-list {
    width: 100%;
    height: auto;
    background-color: #fff;
    padding: 4%;
    box-sizing: border-box;
    margin-top: 20px;
}
.presell-list-ul {
    padding: 10px 0;
}
.presell-list-ul > li {
    display: flex;
    padding: 20px 10px;
    width: 100%;
    box-sizing: border-box;
}
/* 复用样式 */
@import "../../../public/css/movie.css";

.presell-interactive > button {
    background-color: #3c9fe6;
    border: #3c9fe6;
}
.Movie-row-img > img {
    width: 100%;
    height: 100%;
}
</style>

